<template>
  
</template>

<style lang="scss">
.film-list-content {
  background-color: #fff;
  box-sizing: border-box;
  color: #333;
}
.film-item-wrap {
  padding: 15px 15px 15px 0;
  height: 94px;
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
  margin-left: 15px;
  margin-bottom: 0;
  margin-top: 0;
  overflow: hidden;
}
.item-left {
  width: 66px;
  height: 94px;
  background: rgb(249, 249, 249);
  opacity: 1;
  display: block;
  float: left;
  .filmImg {
    width: 66px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }
}
.item-center {
  float: left;
  width: calc(100% - 116px);
  padding: 0 10px;
  box-sizing: border-box;
  .name {
    max-width: calc(100% - 25px);
    color: #191a1b;
    font-size: 16px;
    height: 22px;
    line-height: 22px;
    margin-right: 5px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;
  }
  .item {
    font-size: 9px;
    color: #fff;
    background-color: #d2d6dc;
    height: 14px;
    line-height: 14px;
    padding: 0 2px;
    border-radius: 2px;
    display: inline-block;
    vertical-align: middle;
    white-space: nowrap;
  }
  .label {
    font-size: 13px;
    margin-top: 4px;
    color: #797d82;
    font: 12px/1.5 Tahoma, Helvetica, Arial, sans-serif;
    font-family: Microsoft YaHei, Tahoma, Helvetica, Arial, sans-serif;
  }
  .grade {
    color: #ffb232;
    font-size: 14px;
  }
}
.item-right {
  float: right;

  .buy {
    line-height: 25px;
    height: 25px;
    width: 50px;
    color: #ff5f16;
    font-size: 13px;
    border: 1px solid #ff5f16;
    text-align: center;
    border-radius: 2px;
    // position: relative;
    margin: 25px 0px 0px 0px;
    background: rgb(249, 249, 249);
  }
}
</style>



<script>
import { animate } from "@/utils/index.js";
export default {
  mixins: [animate],
  data() {
    return {
      active: 0,
      film1: {},
      film2: {}
    };
  },
  methods: {},
  mounted() {
    this.$axios
      .get("/maizuo/gateway?pageNum=1&pageSize=10&type=1&k=2956000", {
        params: {
          cityId: 110100
        },
        headers: {
          "X-Client-Info":
            '{"a":"3000","ch":"1002","v":"5.0.4","e":"1586347187635655160275","bc":"110100"}',
          "X-Host": "mall.film-ticket.film.list"
        }
      })
      .then(res => {
        this.film1 = res.data.data.films[0];
      });
    this.$axios
      .get("/maizuo/gateway?pageNum=1&pageSize=10&type=2&k=2086302", {
        params: {
          cityId: 110100
        },
        headers: {
          "X-Client-Info":
            '{"a":"3000","ch":"1002","v":"5.0.4","e":"1586347187635655160275","bc":"110100"}',
          "X-Host": "mall.film-ticket.film.list"
        }
      })
      .then(res => {
        this.film2 = res.data.data.films[0];
      });
  }
};
</script>

